import { Component, AfterViewInit, OnInit, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { FirebaseApp } from '@angular/fire';
import { AngularFireAuth } from "@angular/fire/auth";
import { formatDate } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { DataProviderService } from '../../data-provider/data-provider.service';
import { CommonModule } from '@angular/common';

import * as $ from "jquery";

@Component({
	selector: 'app-article',
	templateUrl: './article.component.html',
	styleUrls: ['./article.component.scss']
})
export class ArticleComponent implements AfterViewInit, OnInit {

	public id: string | undefined;
	public article: any;
	public loading = true;
	public avaliableNews: any = [];
	public currentImageUrl: string | undefined;
	public currentImageIndex: number = 0;

	constructor(private router: Router, private activateRoute: ActivatedRoute,
		private dataProvider: DataProviderService,
		private _router: Router) {
	}

	async ngOnInit(): Promise<void> {
		this.loading = true;
		console.log('ArticleComponent init');

		this.activateRoute.params.subscribe(params => {
			this.id = params['id'];

			let getArticleResult = this.dataProvider.getById(this.id);
			if (getArticleResult.success) {
				this.article = getArticleResult.item;
				this.currentImageUrl = getArticleResult.item.images[0];
			}

			console.log(getArticleResult.item);
			this.loading = false;
		});

		this.avaliableNews = this.dataProvider.getPagedArticles(0, 3);
		
	}

	onImageClick() {
		console.log("OnImageClick");
		if (this.article.images.length === 0 ||
			this.article.images.length === 1)
			return;

		this.currentImageIndex++;
		if (this.currentImageIndex === this.article.images.length)
			this.currentImageIndex = 0;

		this.currentImageUrl = 	this.article.images[this.currentImageIndex];
		
		
		$("#article-image")
		.addClass("img-animation");

		setTimeout(() => {
			$("#article-image").removeClass("img-animation");
		}, 400);
	}

	onAvaliableNewClick(articleId: number){
		this._router.navigate(['/dashboard/article', articleId]);
	}

	ngAfterViewInit() { }
}

